import i18next from 'i18next';

export const lessonStatuses = {
    OUTSTANDING : 'Outstanding',
    ACKNOWLEDGED:'Acknowledged',
    CLEARED:'Cleared'
};

const LessonStatusLabelsMapper = () => {
    return {
        [lessonStatuses.OUTSTANDING]: i18next.t('events.statuses.outstanding'),
        [lessonStatuses.ACKNOWLEDGED]: i18next.t('events.statuses.acknowledged'),
        [lessonStatuses.CLEARED]: i18next.t('events.statuses.cleared')
    };
};

export const getLabelForLessonStatus = (type) => {
    return  type ? LessonStatusLabelsMapper()[type] : '';
};